.nav {
  position: relative;
  top: 0;
  left: 0;
}
@media (min-width: 1200px) {
  .nav {
    padding: 0;
  }
}

.nav--closed .nav__toggle {
  position: absolute;
  top: -48px;
  right: 20px;
  z-index: 2;
  width: 50px;
  height: 24px;
}
.nav--closed .nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 #ffffff, 0 20px 0 #ffffff;
}
@media (min-width: 660px) {
  .nav--closed .nav__toggle {
    top: -67px;
  }
}
@media (min-width: 1200px) {
  .nav--closed .nav__toggle {
    display: none;
  }
}

.nav__toggle:hover::before {
  content: "";
  background-color: #d22856;
  box-shadow: 0 10px 0 #d22856, 0 20px 0 #d22856;
}

.nav__toggle:active::before {
  content: "";
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 10px 0 rgba(255, 255, 255, 0.3), 0 20px 0 rgba(255, 255, 255, 0.3);
}

.nav__list {
  position: absolute;
  top: 0;
  right: 0;
}
@media (min-width: 1200px) {
  .nav__list {
    position: relative;
  }
}

.nav--opened .nav__toggle {
  position: absolute;
  top: -46px;
  right: 20px;
  width: 50px;
  height: 24px;
}
.nav--opened .nav__toggle::before, .nav--opened .nav__toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  left: 10px;
  width: 29px;
  height: 4px;
  background-color: #ffffff;
  border-radius: 0;
  box-shadow: none;
}
.nav--opened .nav__toggle::before {
  transform: rotate(45deg);
}
.nav--opened .nav__toggle::after {
  transform: rotate(-45deg);
}
@media (min-width: 660px) {
  .nav--opened .nav__toggle {
    top: -68px;
  }
}

.nav--opened .nav-list {
  display: block;
}
@media (min-width: 1200px) {
  .nav--opened .nav-list {
    display: flex;
  }
}

.nav--opened .nav__toggle:hover::before, .nav--opened .nav__toggle:hover::after {
  background-color: #d22856;
  box-shadow: none;
}

.nav--opened .nav__toggle:active::before, .nav--opened .nav__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: none;
}

.page-nav--nojs .nav__toggle {
  display: none;
}

.page-nav--nojs .nav-list {
  display: block;
}
@media (min-width: 1200px) {
