  position: relative;
  display: block;
  width: 35px;
  height: 35px;
  background-color: #ffffff;
  outline: 2px solid #e5e5e5;
  border-radius: 1px;
}
.fieldset__mark--radio {
  border-radius: 50%;
}

.fieldset__control[type=checkbox]:checked + .fieldset__mark::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 22px;
  height: 16px;
  background: url("../img/arrow.svg") no-repeat;
}

.fieldset__control[type=radio]:checked + .fieldset__mark::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 15px;
  height: 15px;
  background-color: #d22856;
  border-radius: 50%;
}

.fieldset__text {
  font-size: 18px;
}

.fieldset__textarea {
  width: 100%;
  max-height: 200px;
  font-size: 18px;
  line-height: 158%;
  margin: 0;
  padding: 16px 16px 18px 20px;
  outline: 2px solid #e5e5e5;
  border: none;
  border-radius: 1px;
  box-sizing: border-box;
  resize: none;
}
.fieldset__textarea::placeholder {
  color: #283645;
}
.fieldset__textarea:hover {
  outline: 2px solid #283645;
}
.fieldset__textarea:active {
  outline: 2px solid #d22856;
}
@media (min-width: 660px) {
  .fieldset__textarea {
    width: 81%;
    max-height: 193px;
    margin: 0 auto;
    letter-spacing: -0.2px;
  }
}
@media (min-width: 1200px) {
  .fieldset__textarea {
    width: 87%;
    max-height: 128px;
    margin: 0 auto;
  }
}

.fieldset__label:hover .fieldset__mark {
  outline: 2px solid #283645;
}

.fieldset__label:active .fieldset__mark {
  outline: 2px solid #d22856;
}

